import React from 'react'
import { Card, Input, Col, Row, Select, Checkbox } from 'antd';
import "./CustomerDetails.css";
import Stargold from "../../Assets/images/stargold.png";
import Deletbtn from "../../Assets/images/deletbtn.png";
import Whitestar from "../../Assets/images/whitestar.png";



const { Option } = Select;

function onChange(e) {
    console.log(`checked = ${e.target.checked}`);
}

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const CustomerDetails = () => {
    return (
        <div className="boxfield">
            <Card title="Customer details">
                        <Row gutter={12}>
                                <Col xs={24} sm={8} md={8} lg={8} xl={8} className="mobilemar12">
                                    <Select defaultValue="No title" style={{ marginRight: '1.2rem' }}>
                                        <Option value="No title">No title</Option>
                                        <Option value="Mr.">Mr.</Option>
                                        <Option value="Ms.">Ms.</Option>
                                        <Option value="Mrs.">Mrs.</Option>
                                        <Option value="Miss.">Miss.</Option>
                                        <Option value="Dr.">Dr.</Option>
                                    </Select> 
                                </Col>
                                <Col xs={24} sm={16} md={16} lg={16} xl={16}>
                                    <Row gutter={0} className="inputfieldbox">
                                        <Col xs={24} sm={12} md={12} lg={12} xl={12} className="mobilemar12">
                                            <Input defaultValue="First name" className="borderrightnone"/>
                                        </Col>
                                        <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                            <Input defaultValue="Last name" className="borderleftnone"/> 
                                        </Col>
                                    </Row>  
                                </Col>
                                <Col span={24} style={{paddingTop: "12px"}}>
                                    <Input defaultValue="Company name"/>
                                </Col>
                                <Col span={24} style={{paddingTop: "17px",textAlign: "left"}} className="checkboxfield">
                                    <Checkbox onChange={onChange}>Use company name as the primary name</Checkbox>
                                </Col>
                                <Col span={24} style={{paddingTop: "26px",textAlign: "left"}} className="">
                                    <h4 className="boxsubheading">Contact details</h4>
                                </Col>
                                <Col span={8} style={{paddingTop: "29px"}}>
                                    <div className="starsection">
                                        <div className="starfield">
                                            <img src={Stargold} alt="goldstar"/>
                                        </div>
                                        <Select defaultValue="Home">
                                            <Option value="Main">Main</Option>
                                            <Option value="Home">Home</Option>
                                            <Option value="Work">Work</Option>
                                            <Option value="Fax">Fax</Option>
                                            <Option value="Other">Other</Option>
                                        </Select> 
                                    </div>                                    
                                </Col>
                                <Col span={16} style={{paddingTop: "29px"}}> 
                                    <div className="starsection">
                                        <div className="inputfieldbox">
                                            <Input defaultValue="Phone number"/>
                                        </div> 
                                        <div className="deletfield">
                                            <img src={Deletbtn} alt="goldstar"/>
                                        </div> 
                                    </div>                            
                                </Col>
                                <Col span={8} style={{paddingTop: "14px"}}>
                                    <div className="starsection">
                                        <div className="starfield">
                                            <img src={Whitestar} alt="goldstar"/>
                                        </div>
                                        <Select defaultValue="Work">
                                            <Option value="Work">Work</Option>
                                            <Option value="Personal">Personal</Option>
                                            <Option value="Other">Other</Option>
                                        </Select> 
                                    </div>                                    
                                </Col>
                                <Col span={16} style={{paddingTop: "14px"}}> 
                                    <div className="starsection">
                                        <div className="inputfieldbox">
                                            <Input defaultValue="Phone number"/>
                                        </div> 
                                        <div className="deletfield">
                                            <img src={Deletbtn} alt="goldstar"/>
                                        </div> 
                                    </div>                            
                                </Col>
                                <Col span={8} style={{paddingTop: "14px"}}>
                                    <Select defaultValue="Home" style={{ marginRight: '1.2rem' }}>
                                        <Option value="Main">Main</Option>
                                        <Option value="Home">Home</Option>
                                        <Option value="Work">Work</Option>
                                        <Option value="Fax">Fax</Option>
                                        <Option value="Other">Other</Option>
                                    </Select> 
                                </Col>                                
                                <Col span={16} style={{paddingTop: "14px"}}>
                                    <div className="inputfieldbox">
                                            <Input defaultValue="Phone number"/>
                                    </div>                                    
                                </Col>
                                <Col span={24} style={{paddingTop: "16px",textAlign: "left"}} className="">
                                    <h5 className="inputbottomheading">ADD PHONE NUMBER</h5>
                                </Col>
                                <Col span={8} style={{paddingTop: "22px"}}>
                                    <Select defaultValue="Work" style={{ marginRight: '1.2rem' }}>
                                        <Option value="Work">Work</Option>
                                        <Option value="Personal">Personal</Option>
                                        <Option value="Other">Other</Option>
                                    </Select> 
                                </Col>
                                <Col span={16} style={{paddingTop: "22px"}}>
                                    <div className="inputfieldbox">
                                        <Input defaultValue="Email address"/>
                                    </div>                                    
                                </Col>
                                <Col span={24} style={{paddingTop: "16px",textAlign: "left"}} className="">
                                    <h5 className="inputbottomheading">ADD EMAIL ADDRESS</h5>
                                </Col>
                        </Row>   
            </Card>
        </div>
    )
}

export default CustomerDetails;
